import { useState } from 'react';
import MenuToggle from './menuToggle';
import { Flex, Stack, Box } from '@chakra-ui/react';
import MenuItem from './menuItem';
import Logo from './logo';

const Navbar = () => {
    const [isOpen, setIsOpen] = useState(false)
  
    const toggle = () => setIsOpen(!isOpen)
  
    return(
        <Flex margin={'1%'} marginRight={'2%'} flexDirection={'row'} justifyContent={'space-between'} fontFamily='Lora'>
            <Logo marginLeft={10} />
            <Box
                display={{ base: isOpen ? "block" : "none", md: "block" }}
                flexBasis={{ base: "100%", md: "auto" }}
                marginTop={'1%'}
            >
                <Stack
                    spacing={8}
                    align="center"
                    justify={["center", "space-between", "flex-end", "flex-end"]}
                    direction={["column", "row", "row", "row"]}
                    pt={[4, 4, 0, 0]}
                    >
                    <MenuItem to="/#/">About</MenuItem>
                    <MenuItem to="/#/education">Education</MenuItem>
                    <MenuItem to="/#/research">Research</MenuItem>
                    <MenuItem to="/#/experience">Experience</MenuItem>
                    <MenuItem to="/#/dream">Dream</MenuItem>
                    <MenuItem to="/#/contact">Contact</MenuItem>
                </Stack>
            </Box>
            <MenuToggle toggle={toggle} isOpen={isOpen} />
        </Flex>
    )
  }
  
export default Navbar;